.page {
  background-color: #373f51;
  display: flex;
  justify-content: center;
  color: #fff;
  font-size: 14px;
  padding: 0 2rem;
}
.content {
  max-width: 1200px;
}
.header {
  font-weight: 300;
}
.subHeader {
  color: #9ea2ac;
  font-weight: 300;
  margin-bottom: 0;
}
.section {
  padding: 2rem 0;
  border-bottom: 1px solid #a9bcd0;
  &:last-child {
    border: none;
  }
}
.title {
  font-size: 18px;
  margin-bottom: 1rem;
}
code {
  color: #373737;
  background-color: #dddfe2;
  padding: 0.5rem;
  display: inline-block;
  font-size: 14px;
}
.comment {
  padding: 1rem 0;
  &:last-child {
    padding-bottom: 0;
  }
}
.title + .comment {
  padding-top: 0;
}
.additionalInfo {
  font-size: 13px;
  color: #a9bcd0;
  font-style: italic;
}
button {
  background: #6f73d2;
  border: none;
  padding: 10px 20px;
  color: #fff;
  cursor: pointer;
  border-radius: 3px;
  margin-left: 20px;
  font-size: 16px;
  &:hover {
    background: #7a7eeb;
  }
}
span {
  display: block;
}
ul {
  li {
    padding: 3px 0;
    a {
      text-decoration: none;
      color: #fff;
      font-size: 15px;
    }
  }
}
